* {
  box-sizing: border-box;
  outline: none;
  -webkit-overflow-scrolling: touch; }

a {
  color: inherit;
  text-decoration: none;
  &:hover, &:focus {
    text-decoration: none; } }

ul {
  list-style: none;
  li {
    display: inline-block; } }

body, html {
  background: white;
  color: $fontColor;
  font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 16px;
  min-width: 320px;
  max-width: 100%;
  margin: 0;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  &__content {
    flex: 1 0 auto; } }


.container {
  width: 100%;
  max-width: 1200px;
  padding-left: 40px;
  padding-right: 40px; }
