// buttons
button {
  display: inline-block;
  -webkit-apperance: none;
  border: 0;
  box-shadow: none;
  &:focus {
    outline: none; } }

.btn {
  display: inline-block;
  position: relative;
  text-align: center;
  cursor: pointer;
  font-size: 1.4rem;
  transition: all $transitionSpeed ease-in-out;
  &:focus, &:active {
    outline: none; }
  &--block {
    display: block;
    width: 100%; } }
